	module.exports = {"data":{"markdownRemark":{"html":"<p>早朝に、私は「中毒」の最後の2つのエピソードを見て、光と栄光を見ました。人生はいつも不明なものであるとは限らず、社会は完全に冷笑的な人物ではありません。 2人のマスターはついに真実の愛を実現したので、すぐに再会しましたが、突然筋萎縮病を患ってしまったので、恥ずかしいでしょう。だから私は女性の犠牲、考えの終わり、そして女性を負うことはできません。女性は実際に真剣に理解していませんでした。彼の親友も彼を殴った。すべてが昔ながらの正義感です。女性は最終的に何が起こっているかを理解しました。男はまた部屋に戻った。誰もが数十枚の「愛」の言葉を新聞から黒板に書いておき、両者はお互いを抱き合わせて暖かくキスする。その男は暖かいキスをした。すべてがとても自然でとても感動的です。それは特別な道ではありませんが、普通の人にとって最も必要な道です。これは人生です。これは人生で必要とされる光の栄光です。</p>\n<p>小さなFが来て、トニックである「中国のもち米」を食べました。最近、トニック、チキンエッセンス、ヘビエッセンスなどがますます登場しています。「人間の罰金」の導入は、おそらく最も補完的です。これはまた、生活水準が向上したことを示しています。過去の人々は強壮剤を食べるものがあります。田舎では、砂糖を飲むことは、すでに非常に \"贅沢\"です。人生が良ければ、人生は長生きします。人生が生きたくないときは、より長く生きたいです。人生は良いです、もっと長く生きる必要があります。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0028.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0028.md","frontmatter":{"title":"2月9日 水曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0028.md absPath of file >>> MarkdownRemark"}}

